.weHelp {
	background-color: #212c53;
	&__container {
		padding: 30px 15px;
		display: flex;
		flex-direction: column;
		gap: 20px;
	}

	& .title {
		padding-left: 40px;
		position: relative;
		margin-bottom: 10px;
		&::before {
			position: absolute;
			content: "";
			width: 20px;
			height: 20px;
			border-radius: 50%;
			top: 50%;
			transform: translateY(-50%);
			left: 0;
			border: 2px solid #64c1c6;
		}
	}

	& .text {
		max-width: 550px;
	}

	&__cardsWrapper {
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		gap: 10px 16px;
	}

	&__card {
		& .image {
			width: 137px;
			height: 114px;
			object-fit: cover;
			margin-bottom: 5px;
		}
		& .text {
			text-transform: uppercase;
			text-align: center;
		}
	}

	@media screen and (min-width: 768px) {
		& .title {
			max-width: 420px;
			&::before {
				width: 30px;
				height: 30px;
			}
		}
		&__cardsWrapper {
			gap: 10px 12px;
		}

		&__card .image {
			min-width: 112px;
			min-height: 113px;
		}
	}

	@media screen and (min-width: 1440px) {
		&__cardsWrapper {
			gap: 30px 20px;
		}
		&__card .image {
			width: auto;
			height: auto;
			width: 400px;
			height: 190px;
		}
	}
}
